___CSS_LOADER_EXPORT___.push([module.id, `:root{
  --main-logo-color: #3E1A1A;
  --main-header-color:#3e1a1aa0;
  --dark-background-color: rgb(28, 28, 28);
  --main-red-color: #9A192F;
  --main-text-color: #FFFFFD;
}
*{
  box-sizing: border-box;
  margin: 0px;
  font-family: "Roboto Condensed", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}
body{
  background: var(--dark-background-color) center;
}
header{
  position: sticky;
  inset: 0px 0px 90% 0px;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to bottom, var(--main-header-color), var(--dark-background-color));
  z-index: 998;
}
header .logo {
  width: 10%;
  height: 10%;
  scale: 0.7;
  cursor: pointer;
}

.mobile-dash-btn{
  display: none;
}

header ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: center;
  flex: 1, 0, 200px;
  margin-right: clamp(4rem, 5rem, 6rem);
}

header ul li button {
  position: relative;
  font-weight: bold;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.2rem;
  color: var(--main-text-color);
  display: block;
    transition: scale 0.3s ease;
}

header ul li button::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  width: 0;
  background-color: var(--main-text-color);
  transition: width 0.3s ease-in;
  border-radius: 1px;
}

header ul li button:hover{
  scale: 1.1;
}

header ul li button.active-tab::after {
  width: 100%;
}

main{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.home{
  display: flex;
  justify-content: space-evenly;
  gap: 4rem;
  margin: clamp(2rem, 3rem, 5rem);
}

.intro-home {
  gap: 15%;
}

.home > img {
  width: 40%;
  height: 20%;
  position: relative;
  border-radius: 50px;
  box-shadow: -10px 10px 20px 10px rgba(0, 0, 0, 0.426);
}

.home aside p{
  width: clamp(60%, 80%, 90%);
  color: var(--main-text-color);
  font-size: clamp(1.1rem,1.4rem,2rem);
}

.home aside h1 {
  font-size: clamp(1.8rem, 3rem, 4rem);
  margin-bottom: 1rem;
  color: var(--main-text-color);
}

.end-home {
  display: flex;
  justify-content: space-around;
  gap: clamp(4rem, 10rem, 16rem);
  margin-top: clamp(1rem, 3rem, 6rem);
  padding: 2rem;
}

.i-cont{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 1rem;
  color: var(--main-text-color);
}
.i-cont img{
  cursor: pointer;
  scale: 1.4;
}

.site-footer {
  background: linear-gradient(to top, var(--main-header-color), var(--dark-background-color));
  color: var(--main-text-color);
  text-align: center;
  padding: 2rem 1rem;
  font-size: 0.9rem;
  margin-top: clamp(10vh, 25vh, 30vh);
}

.footer-content {
  max-width: 800px;
  margin: 0 auto;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 1rem 0;
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.footer-links a {
  color: #6f2f2f;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: var(--main-text-color);
}

.footer-tagline {
  font-style: italic;
  font-size: 0.8rem;
  opacity: 0.8;
}

.mobileSidebar{
  display: none;
}




/* menu item styling */


.menu-container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  padding: 1rem;
}

.menu-item {
  background-color: var(--main-logo-color);
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.3);
  display: flex;
  flex-direction: column;
}

.menu-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.menu-content {
  padding: 1rem;
}

.menu-name {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.menu-desc {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: #ddd;
}

.menu-price {
  font-weight: bold;
  font-size: 1.2rem;
  color: var(--main-red-color);
}

/*review section styling*/

.reviews-section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 70vh;
  color: var(--main-text-color);
  margin: 2rem 1rem;
}


.tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.tab-btn {
  flex: 1;
  padding: 0.5rem 1rem;
  background-color: var(--main-header-color);
  border: none;
  border-radius: 0.5rem 0.5rem 0 0;
  cursor: pointer;
  color: var(--main-text-color);
  font-weight: bold;
  transition: background 0.3s;
}

.tab-btn.active-tab {
  background-color: var(--main-red-color);
}


.tab-content {
  background-color: var(--main-logo-color);
  padding: 1rem;
  border-radius: 0 0 0.5rem 0.5rem;
  display: none;
  flex: 1;
  overflow-y: auto; 
}

.tab-content.active-tab {
  display: flex;
  flex-direction: column;
}

form {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

form label {
  display: block;
  margin-top: 0.5rem;
  margin-bottom: 0.2rem;
}

form input,
form select,
form textarea {
  width: 100%;
  padding: 0.5rem;
  border-radius: 0.3rem;
  border: none;
}

form button {
  background-color: var(--main-red-color);
  color: var(--main-text-color);
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  border-radius: 0.3rem;
  font-weight: bold;
}

form button:hover {
  background-color: #bf1f42;
}

.review-item {
  margin-bottom: 0.5rem;
}


@media (max-width: 1000px) {
  header {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0.6rem;
  }

  header > .logo {
    width: 30%;
    height: auto;
    margin-bottom: 1rem;
  }

  header ul {
    display: none;
  }

  main {
    width: 100%;
    padding: 0 1rem;
  }

  .home {
    flex-direction: column;
    gap: 1.5rem;
    margin: 1rem 0;
    align-items: center;
  }

  .home > img {
    width: 80%;
    height: auto;
    border-radius: 20px;
    box-shadow: -5px 5px 15px 5px rgba(0, 0, 0, 0.3);
  }

  .home aside p {
    width: 100%;
    font-size: 1rem;
  }

  .home aside h1 {
    font-size: 1.8rem;
  }

  .end-home {
    flex-direction: column;
    gap: 2rem;
    margin-top: 2rem;
    padding: 1rem;
  }

  .i-cont {
    flex-direction: row;
    gap: 1rem;
    text-align: left;
    justify-content: space-between;
  }

  .i-cont img {
    scale: 1;
  }

  .site-footer {
    padding: 1rem;
    font-size: 0.8rem;
    margin-top: 10vh;
  }

  .footer-links {
    flex-direction: row;
    justify-content: center;
  }

  /* Dashboard Styling */
  .mobile-dash-btn{
    display: block;
    width: 10vw;
    height: 10vh;
  } 

  .mobileSidebar{
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    gap: 5vh;
    position: fixed;
    z-index: 9999;
    left: 60%;
    background: linear-gradient(to top, var(--main-logo-color), var(--dark-background-color));
    top: 0;
    bottom: 0;
    right: 0;
    
  }

  .mobile-close-dash-btn{
    width: 10vw;
    height: 10vh;
  }

  .x-cont{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-color: var(--dark-background-color);
  }

  .mobileSidebar.inactive{
    display: none;
  }


  .mobileSidebar.active ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: center;
    flex: 1, 0, 200px;
    margin-right: clamp(4rem, 5rem, 6rem);
  }

  .mobileSidebar.active ul li {
    padding: 1rem;
  }

  .mobileSidebar.active ul li button {
    position: relative;
    font-weight: bold;
    cursor: pointer;
    background: none;
    border: none;
    font-size: 1.2rem;
    color: var(--main-text-color);
    display: block;
  }

  .mobileSidebar.active ul li button::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 2px;
    width: 0;
    background-color: var(--main-text-color);
    transition: width 0.3s ease-in;
    border-radius: 1px;
  }

  .mobileSidebar.active ul li button:focus::after {
    width: 100%;
  }

  main.paused{
    pointer-events: none;
  }

}

/* mobile responsive menu items */

@media (min-width: 768px) {
  .menu-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* 3 items per row for large screens */
@media (min-width: 1200px) {
  .menu-container {
    grid-template-columns: repeat(3, 1fr);
  }
}


