r\nheader ul li button.active-tab::after {\r\n  width: 100%;\r\n}\r\n\r\nmain{\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  position: relative;\r\n}\r\n\r\n.home{\r\n  display: flex;\r\n  justify-content: space-evenly;\r\n  gap: 4rem;\r\n  margin: clamp(2rem, 3rem, 5rem);\r\n}\r\n\r\n.intro-home {\r\n  gap: 15%;\r\n}\r\n\r\n.home > img {\r\n  width: 40%;\r\n  height: 20%;\r\n  position: relative;\r\n  border-radius: 50px;\r\n  box-shadow: -10px 10px 20px 10px rgba(0, 0, 0, 0.426);\r\n}\r\n\r\n.home aside p{\r\n  width: clamp(60%, 80%, 90%);\r\n  color: var(--main-text-color);\r\n  font-size: clamp(1.1rem,1.4rem,2rem);\r\n}\r\n\r\n.home aside h1 {\r\n  font-size: clamp(1.8rem, 3rem, 4rem);\r\n  margin-bottom: 1rem;\r\n  color: var(--main-text-color);\r\n}\r\n\r\n.end-home {\r\n  display: flex;\r\n  justify-content: space-around;\r\n  gap: clamp(4rem, 10rem, 16rem);\r\n  margin-top: clamp(1rem, 3rem, 6rem);\r\n  padding: 2rem;\r\n}\r\n\r\n.i-cont{\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  text-align: center;\r\n  gap: 1rem;\r\n  color: var(--main-text-color);\r\n}\r\n.i-cont img{\r\n  cursor: pointer;\r\n  scale: 1.4;\r\n}\r\n\r\n.site-footer {\r\n  background: linear-gradient(to top, var(--main-header-color), var(--dark-background-color));\r\n  color: var(--main-text-color);\r\n  text-align: center;\r\n  padding: 2rem 1rem;\r\n  font-size: 0.9rem;\r\n  margin-top: clamp(10vh, 25vh, 30vh);\r\n}\r\n\r\n.footer-content {\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.footer-links {\r\n  list-style: none;\r\n  padding: 0;\r\n  margin: 1rem 0;\r\n  display: flex;\r\n  gap: 1rem;\r\n  justify-content: center;\r\n}\r\n\r\n.footer-links a {\r\n  color: #6f2f2f;\r\n  text-decoration: none;\r\n  transition: color 0.3s ease;\r\n}\r\n\r\n.footer-links a:hover {\r\n  color: var(--main-text-color);\r\n}\r\n\r\n.footer-tagline {\r\n  font-style: italic;\r\n  font-size: 0.8rem;\r\n  opacity: 0.8;\r\n}\r\n\r\n.mobileSidebar{\r\n  display: none;\r\n}\r\n\r\n\r\n\r\n\r\n/* menu item styling */\r\n\r\n\r\n.menu-container {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 1rem;\r\n  padding: 1rem;\r\n}\r\n\r\n.menu-item {\r\n  background-color: var(--main-logo-color);\r\n  border-radius: 0.5rem;\r\n  overflow: hidden;\r\n  box-shadow: 0 2px 8px rgba(0,0,0,0.3);\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.menu-img {\r\n  width: 100%;\r\n  height: 200px;\r\n  object-fit: cover;\r\n}\r\n\r\n.menu-content {\r\n  padding: 1rem;\r\n}\r\n\r\n.menu-name {\r\n  font-size: 1.5rem;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.menu-desc {\r\n  font-size: 1rem;\r\n  margin-bottom: 0.5rem;\r\n  color: #ddd;\r\n}\r\n\r\n.menu-price {\r\n  font-weight: bold;\r\n  font-size: 1.2rem;\r\n  color: var(--main-red-color);\r\n}\r\n\r\n/*review section styling*/\r\n\r\n.reviews-section {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: space-between;\r\n  min-height: 70vh;\r\n  color: var(--main-text-color);\r\n  margin: 2rem 1rem;\r\n}\r\n\r\n\r\n.tabs {\r\n  display: flex;\r\n  gap: 0.5rem;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.tab-btn {\r\n  flex: 1;\r\n  padding: 0.5rem 1rem;\r\n  background-color: var(--main-header-color);\r\n  border: none;\r\n  border-radius: 0.5rem 0.5rem 0 0;\r\n  cursor: pointer;\r\n  color: var(--main-text-color);\r\n  font-weight: bold;\r\n  transition: background 0.3s;\r\n}\r\n\r\n.tab-btn.active-tab {\r\n  background-color: var(--main-red-color);\r\n}\r\n\r\n\r\n.tab-content {\r\n  background-color: var(--main-logo-color);\r\n  padding: 1rem;\r\n  border-radius: 0 0 0.5rem 0.5rem;\r\n  display: none;\r\n  flex: 1;\r\n  overflow-y: auto; \r\n}\r\n\r\n.tab-content.active-tab {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\nform {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 0.5rem;\r\n}\r\n\r\nform label {\r\n  display: block;\r\n  margin-top: 0.5rem;\r\n  margin-bottom: 0.2rem;\r\n}\r\n\r\nform input,\r\nform select,\r\nform textarea {\r\n  width: 100%;\r\n  padding: 0.5rem;\r\n  border-radius: 0.3rem;\r\n  border: none;\r\n}\r\n\r\nform button {\r\n  background-color: var(--main-red-color);\r\n  color: var(--main-text-color);\r\n  border: none;\r\n  padding: 0.5rem 1rem;\r\n  cursor: pointer;\r\n  border-radius: 0.3rem;\r\n  font-weight: bold;\r\n}\r\n\r\nform button:hover {\r\n  background-color: #bf1f42;\r\n}\r\n\r\n.review-item {\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n\r\n@media (max-width: 1000px) {\r\n  header {\r\n    flex-direction: row;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 0.6rem;\r\n  }\r\n\r\n  header > .logo {\r\n    width: 30%;\r\n    height: auto;\r\n    margin-bottom: 1rem;\r\n  }\r\n\r\n  header ul {\r\n    display: none;\r\n  }\r\n\r\n  main {\r\n    width: 100%;\r\n    padding: 0 1rem;\r\n  }\r\n\r\n  .home {\r\n    flex-direction: column;\r\n    gap: 1.5rem;\r\n    margin: 1rem 0;\r\n    align-items: center;\r\n  }\r\n\r\n  .home > img {\r\n    width: 80%;\r\n    height: auto;\r\n    border-radius: 20px;\r\n    box-shadow: -5px 5px 15px 5px rgba(0, 0, 0, 0.3);\r\n  }\r\n\r\n  .home aside p {\r\n    width: 100%;\r\n    font-size: 1rem;\r\n  }\r\n\r\n  .home aside h1 {\r\n    font-size: 1.8rem;\r\n  }\r\n\r\n  .end-home {\r\n    flex-direction: column;\r\n    gap: 2rem;\r\n    margin-top: 2rem;\r\n    padding: 1rem;\r\n  }\r\n\r\n  .i-cont {\r\n    flex-direction: row;\r\n    gap: 1rem;\r\n    text-align: left;\r\n    justify-content: space-between;\r\n  }\r\n\r\n  .i-cont img {\r\n    scale: 1;\r\n  }\r\n\r\n  .site-footer {\r\n    padding: 1rem;\r\n    font-size: 0.8rem;\r\n    margin-top: 10vh;\r\n  }\r\n\r\n  .footer-links {\r\n    flex-direction: row;\r\n    justify-content: center;\r\n  }\r\n\r\n  /* Dashboard Styling */\r\n  .mobile-dash-btn{\r\n    display: block;\r\n    width: 10vw;\r\n    height: 10vh;\r\n  } \r\n\r\n  .mobileSidebar{\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content:flex-start;\r\n    gap: 5vh;\r\n    position: fixed;\r\n    z-index: 9999;\r\n    left: 60%;\r\n    background: linear-gradient(to top, var(--main-logo-color), var(--dark-background-color));\r\n    top: 0;\r\n    bottom: 0;\r\n    right: 0;\r\n    \r\n  }\r\n\r\n  .mobile-close-dash-btn{\r\n    width: 10vw;\r\n    height: 10vh;\r\n  }\r\n\r\n  .x-cont{\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: flex-end;\r\n    background-color: var(--dark-background-color);\r\n  }\r\n\r\n  .mobileSidebar.inactive{\r\n    display: none;\r\n  }\r\n\r\n\r\n  .mobileSidebar.active ul {\r\n    list-style: none;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 2rem;\r\n    justify-content: center;\r\n    flex: 1, 0, 200px;\r\n    margin-right: clamp(4rem, 5rem, 6rem);\r\n  }\r\n\r\n  .mobileSidebar.active ul li {\r\n    padding: 1rem;\r\n  }\r\n\r\n  .mobileSidebar.active ul li button {\r\n    position: relative;\r\n    font-weight: bold;\r\n    cursor: pointer;\r\n    background: none;\r\n    border: none;\r\n    font-size: 1.2rem;\r\n    color: var(--main-text-color);\r\n    display: block;\r\n  }\r\n\r\n  .mobileSidebar.active ul li button::after {\r\n    content: '';\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    height: 2px;\r\n    width: 0;\r\n    background-color: var(--main-text-color);\r\n    transition: width 0.3s ease-in;\r\n    border-radius: 1px;\r\n  }\r\n\r\n  .mobileSidebar.active ul li button:focus::after {\r\n    width: 100%;\r\n  }\r\n\r\n  main.paused{\r\n    pointer-events: none;\r\n  }\r\n\r\n}\r\n\r\n/* mobile responsive menu items */\r\n\r\n@media (min-width: 768px) {\r\n  .menu-container {\r\n    grid-template-columns: repeat(2, 1fr);\r\n  }\r\n}\r\n\r\n/* 3 items per row for large screens */\r\n@media (min-width: 1200px) {\r\n  .menu-container {\r\n    grid-template-columns: repeat(3, 1fr);\r\n  }\r\n}\r\n\r\n\r\n"],"sourceRoot":""}]);
